body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  background-color: rgb(35, 42, 47);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0; 
}

.bp3-slider-handle:focus {
  outline: 0;
}
.App {
  text-align: left;
  margin: 4px;
  margin-bottom: -30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* bluepring style overwrites*/
.bp3-slider-label{
  transform:translate(0%, 20px);
}

.bp3-slider-axis :nth-child(2){
  transform:translate(-100%, 20px);
}

.bp3-slider-axis :nth-child(3){
  transform:translate(-100%, 20px);

  right: 0 px;
}

.bp3-slider-handle .bp3-slider-label{
  transform:translate(-50%, -20px);
}

.bp3-tab-list {
  flex-flow: row wrap;
}

.bp3-overlay {
  pointer-events: none;
}

/* gui styles */
.parameter-label {
  white-space: nowrap;
  color: gray;
  font-size: 16;  
  margin-bottom: 4px;
}

.parameter-wrapper {
  padding-top: 4px;
  padding-bottom: 0px;  
}

.parameter-wrapper .inner{
  padding: 5px;
  border-radius: 4px;
}

.rootgroup-wrapper {
  padding: 5px;
  padding-top: 0px;
  padding-bottom: 4px;
  border-radius: 4px;
  border: 1px solid #454545;
}

.serverid {
  font-size: 0.8em;
  margin-top: 5px;
  margin-left: 2px;
}

.credits {
  font-size: 0.8em;
  margin-top: 20;
  margin-bottom: 5;
  width: 100%;
  text-align: center;
  font-size: 0.8em;
}
